.about {
    height: 100vh;
    display: flex;
    align-items: center;
}

.about-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.about-right {
    flex: 1;
}

.about-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}
.about-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.about-img {
    width: 200%;
    height: 100%;
}

.about-title {
    font-weight: 400;
}

.about-subtitle {
    margin: 20px 0;
}

.about-description {
    font-weight: 300;
}

.about-award {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-award-img {
    width: 120px;
    height: 120px;
    border-radius: 20px;
}

.about-award-texts {
    width: 70%;
}

.about-award-title {
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}

@media screen and (max-width: 767px) {
    .about {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .about-left {
        width: 100%;
    }

    .about-card {
        height: 30vh;;
    }

    .about-card.bg, .about-award {
        display: none;
    }

    .about-right {
        padding: 20px;
    }
}